window.initAttempCount = 0;

const initFirebse = async () => {
  try {
    if (firebase.apps.length === 0) {
      firebase.initializeApp(firebaseConfig);
    }
    firebase.analytics();
    firebase.analytics().logEvent('test');
  } catch (e) {
    console.log(' unable to init firabse ', e);
    if (window.initAttempCount < maxInitTry) {
      window.initAttempCount += 1;
      initFirebse();
    }
  }
};

const Layout = ({ children }) => {
  React.useEffect(() => {
    initFirebse();
  }, []);
  return (
    <>
      <Header />
      <main className="text-gray-900">{children}</main>
      {/* <Footer /> */}
    </>
  );
};

export default Layout;
